body {
  background-image: url(/JessicaLim8/PersonalWebsite/static/media/background.a1cbd36a.png);
  background-color: #85F1D7; 
  color: white;
  font-family: 'B612';
}

h1 {
  font-size: 5ex;
}

p {
  font-size: 2ex;
}

.landing {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  /* background-color: #2B3965; */
  height: 90vh;
  padding: 20px;
}

.iconBar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-self: right;
          align-self: right;
  font-size: 3em;
}

.mainInfo {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-self: center;
          align-self: center;
}

.textBox {
  margin: 10px;
  -webkit-align-self: center;
          align-self: center;
  max-width: 400px;
}

#headshot {
  width: 20%;
  height: 20%;
  border-radius: 45px;
  margin: 10px;
}

#hover {
  color: black;
}

#not-hover {
  color: white;
}


